export const RepoDiscribe = ({ repo }: any) => {
    const starsCount = repo?.stargazers_count?.toString().split('').splice(0, 2).join('');
  return (
    <div className="d-flex p-3 align-items-center">
      <div style={{margin: '0 20px 0 0'}}><h3>{repo.full_name}</h3></div>
      <div className='d-flex'>
        <svg
        xmlns="http://www.w3.org/2000/svg"
        width="20"
        height="20"
        fill="currentColor"
        className="bi bi-star-fill"
        viewBox="0 0 16 16"
      >
        <path d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.282.95l-3.522 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z" />
      </svg>
     
      <div className="ms-2">{starsCount}K</div>
      <i className="ms-2">Stars</i>
      </div>
    </div>
  );
};
